import segmentedControl from '../components/tabbar-own/tabbar-own.vue';
import neilModal from '../../components/neil-modal/neil-modal.vue';
import uniTag from '../../components/uni-tag/uni-tag.vue';
import uniSection from '../../components/uni-section/uni-section.vue';
import uniNoticebar from '../../components/uni-notice-bar/uni-notice-bar.vue';
import timeSelector from '../../components/wing-time-selector/wing-time-selector.vue';
export default {
	components: {
		segmentedControl,
		neilModal,
		uniTag,
		uniSection,
		uniNoticebar,
		timeSelector
	},
	beforeCreate() {
		// console.log(页面创建前);
	},
	onLoad(e) {
		// console.log(e);
	},
	data() {
		return {
			// tab选项卡信息
			items: ['主播列表', '主播数据'],
			current: 0,
			// 添加主播模态框是否显示
			show4: false,
			// 主播列表信息
			zblist: [],
			// 是否有主播列表
			haszblist: 1,
			// 主播收入数据信息
			zbdata: [],
			// 是否有主播收益数据
			haszbdata: 1,
			// 排行榜信息
			ranklist: [],
			title: '当前选择器',
			time: new Date().toLocaleDateString(),
			// 查询收益的起止时间
			beginDate: '',
			endDate: '',
			// 登录的token
			token: '',
			// 添加的主播信息
			anchorInfo: {
				// 添加的账号
				third_user_id: '',
				// 真实姓名
				real_name: '',
				// 手机号
				mobile: ''
			},
			// noticebar文字信息
			rank_string: '',
			// 查询时间
			date:'',
			// 查询页码
			page:''
		};
	},
	onLoad() {},
	onShow() {
		this.getToken();
		this.getZblist();
		this.getRanklist();
		this.setNoticebar();
		this.getZbdata();
	},
	methods: {
		getToken() {
			this.token = uni.getStorageSync('token');
		},
		setNoticebar() {
			console.log(this.$refs.noticebar.text)
			this.$refs.noticebar.text = this.rank_string
			console.log(this.$refs.noticebar.text)
		},
		// 获取主播列表信息
		getZblist() {
			const that = this;
			uni.request({
				url: 'http://www.vzoyo.com/api/anchor/lists',
				header: { token: this.token },
				success(res) {
					if (res.data.data.list) {
						that.zblist = res.data.data.list;
						that.rank_string = res.data.data.rank_string;
						that.haszblist = 1;
					} else {
						that.haszblist = 0;
					}
				}
			});
			// 获取的返回值如果有 就把haszblist赋值为1，否则赋值为0
		},
		// 获取主播收益数据
		getZbdata() {
			// console.log('获取主播收益信息');
			uni.request({
				url:'http://www.vzoyo.com/api/anchor/AnchorData',
				data:{date:this.date,page:this.page},
				header: { token: this.token },
				success(res) {
				}
			})
		},
		// 获取排行榜数据
		getRanklist() {
			const that = this;
			uni.request({
				url: 'http://www.vzoyo.com/api/Rank/yesterList',
				header: { token: this.token },
				success(res) {
					that.ranklist = res.data.data;
				}
			});
		},
		// 点击跳转到排行榜列表页
		navagateto() {
			uni.navigateTo({
				url: '`./ranklist/ranklist?`+encodeURIComponent(JSON.stringify(this.ranklist))'
			});
		},
		/**
		 * 点击segmentedControl 事件回调
		 */
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		// 点击添加新主播，调出模态框
		addzb() {
			this.iShow = true;
		},
		// 关于模态框的函数
		bindClick() {
			this.show4 = true;
		},
		closeModal() {
			this.show4 = false;
			console.log(this.show4);
		},
		bindBtn(type) {
			this.show4 = false;
		},
		addzbinfo() {
			// 发请求提交主播信息
			console.log('提交');
			// 先做表单校验，再发请求提交
			uni.request({
				url: 'http://www.vzoyo.com/api/anchor/create',
				method: 'POST',
				data: this.anchorInfo,
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					token: this.token
				},
				success(res) {
					this.anchorInfo = {};
					this.getZblist();
					this.show4 = false;
				}
			});
			// 提交后重新获取主播列表数据
		},
		// 时间选择器相关函数
		btnConfirm(e) {
			// console.log('确定时间为：', e);
			this.time = e.value;
			this.title = '当前选择时间';
		},
		btnCancel() {
			// console.log('取消时间：');
		}
	}
};
